.sheet {
  &.profile-section {
    background-color: #f1f5f5;
    .content {
      > div {
        min-width: 20%;
      }
      display: flex;
      //grid-template-columns: 1fr 40% 1fr;
      //grid-template-columns: repeat(auto-fit, minmax(20%, 1fr));
      margin-top: 2%;
      align-items: center;
      justify-content: space-around;
      flex-direction: row;
      flex-wrap: wrap;
      gap: 1rem;

      .webDevelopementLeft {
        display: flex;
        justify-content: flex-start;
        flex-grow: 1;
        img {
          height: 200px;
          width: 200px;
        }
      }
      .profile-description {
        flex-grow: 2;
        max-width: 50%;
        p {
          font-size: 1.2rem;
          text-align: justify;
        }
      }
      .webDevelopementRight {
        display: flex;
        justify-content: flex-end;
        flex-grow: 1;
        img {
          height: 200px;
          width: 200px;
        }
      }
    }
  }

  &.skills {
    .content {
      display: grid;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      gap: 2rem;
      transition: box-shadow 0.9s linear;
      justify-content: space-around;
      .card {
        background: #256006;
        min-width: 20%;
        max-width: 30%;
        min-height: 20vh;
        text-align: center;
        padding: 0;
        border-color: #256006;
        border: unset;
        box-shadow: 0px 0px 3px 2px #c1c1c1;

        .card-header {
          flex-direction: row;
          justify-content: stretch;
          background: #fff;
          color: #000;
          //border: 1px #256006 solid;
          border-radius: 7px 7px 0 0;
        }
        .card-content {
          display: flex;
          flex-flow: column;
          gap: 1rem;
          padding: 1rem;
          background: #256006;
          color: #fff;
          border-radius: 0 0 8px 8px;

          .expertise {
            display: flex;
            flex-flow: row;
            gap: 1rem;
            align-items: center;
            justify-content: center;
          }
          span {
            display: flex;
            background: black;
            width: 30px;
            height: 30px;
            align-items: center;            
            justify-content: center;
            border-radius: 16px;
          }
        }
        &:hover {
          box-shadow: 0px 0px 3px 2px #4fc911;
          cursor: pointer;
          border-color: #4fc911;
        }
      }
    }
  }
}
